import {
  ChangeEvent,
  HTMLInputTypeAttribute,
  KeyboardEvent,
  ReactNode,
  memo,
  useCallback,
  useEffect,
  useState
} from 'react';
import { Controller, useFormContext } from 'react-hook-form';
import { NumberFormatBase } from 'react-number-format';

import _get from 'lodash/get';
import {
  formatToKoreanAmount,
  currencyFormatter,
  dateFormatter,
  dateToTodayFormatter,
  telFormatter
} from 'utils/formatters';

import classNames from 'classnames';
import ErrorMessage from 'components/forms/ErrorMessage';
import Label from 'components/forms/Label';
import Icon from 'components/ui/Icon';
import Typography from 'components/ui/Typography';
import './index.scss';

// ----------------------------------------------------------------------

export type InputProps = {
  name: string;
  placeholder?: string;
  type?: HTMLInputTypeAttribute | 'custom-date' | 'date-to-today' | 'phone-number' | 'currency';
  disabled?: boolean;
  readOnly?: boolean;
  label?: ReactNode;
  value?: string | number;
  defaultValue?: string | number;
  labelWidth?: string;
  required?: boolean;
  flexDirection?: 'row' | 'column';
  variant?: 'default' | 'bordered' | 'ghost';
  size?: 'lg' | 'default' | 'sm';
  align?: 'left' | 'center' | 'right';
  color?: 'primary' | 'secondary';
  unit?: string;
  outside?: boolean;
  iconName?: IconTypes;
  clearable?: boolean;
  onClick?: () => void;
  onChange?: (
    e: ChangeEvent<HTMLInputElement> | { target: { name: string; value: string | number } }
  ) => void;
  autoComplete?: 'on' | 'off';
  hasContext?: boolean;
  hasNegativeNumbers?: boolean;
  showCurrencyValue?: boolean;
};

const COLOR_MAPS = {
  default: '',
  primary: 'text-field--primary',
  secondary: 'text-field--secondary'
};

const VARIANT_MAPS = {
  default: '',
  bordered: 'input-bordered text-field--bordered',
  ghost: 'input-ghost text-field--ghost'
};

const SIZE_MAPS = {
  default: 'text-field--default',
  lg: 'text-field--lg',
  sm: 'text-field--sm'
};

const ALIGN_MAPS = {
  left: 'text-field--left',
  center: 'text-field--center',
  right: 'text-field--right'
};

export const ContextInput = ({
  name,
  placeholder = '',
  type = 'text',
  disabled = false,
  readOnly = false,
  label,
  required = false,
  defaultValue,
  flexDirection = 'column',
  variant = 'bordered',
  size = 'default',
  align = 'left',
  color = 'primary',
  unit,
  outside = false,
  iconName,
  clearable = false,
  onClick,
  onChange,
  autoComplete = 'off',
  hasNegativeNumbers = false,
  showCurrencyValue = false,
  ...props
}: InputProps) => {
  const { control, reset, formState, watch, setValue, getValues } = useFormContext();

  const errorMessage = _get(formState?.errors, `${name}.message`, '') as string;

  const [isNegativeNumber, setIsNegativeNumber] = useState(false);

  const onKeyDown = useCallback((e: KeyboardEvent<HTMLInputElement>) => {
    const { code } = e;

    if (code === 'Minus') {
      setIsNegativeNumber((prev) => !prev);
    }
  }, []);

  const format = useCallback((type: InputProps['type']) => {
    switch (type) {
      case 'custom-date':
        return dateFormatter;
      case 'date-to-today':
        return dateToTodayFormatter;
      case 'phone-number':
        return telFormatter;
      case 'currency':
        return currencyFormatter;
      default:
        break;
    }
  }, []);

  const onInputChange = useCallback(
    (value: string) => {
      switch (type) {
        case 'currency': {
          const currencyValue = Number(value);
          const transformedNegativeValue = -Math.abs(currencyValue);

          if (isNegativeNumber) {
            return setValue(name, transformedNegativeValue);
          }
          return setValue(name, value);
        }
        case 'date-to-today': {
          const transformedValue = Number(value.replace(/-/g, ''));
          return setValue(name, transformedValue);
        }
        case 'phone-number': {
          const transformedValue = value.replace(/-/g, '');
          return setValue(name, transformedValue);
        }
        default: {
          return setValue(name, value);
        }
      }
    },
    [isNegativeNumber, type, setValue, name]
  );

  useEffect(() => {
    if (isNegativeNumber) {
      switch (type) {
        case 'currency': {
          const currentValue = getValues(name);

          if (typeof currentValue === 'string') {
            const transformedValue = Number(currentValue.replace(/,/g, ''));
            const transformedNegativeValue = -Math.abs(transformedValue);
            if (!isNaN(transformedValue)) {
              setValue(name, isNegativeNumber ? transformedNegativeValue : transformedValue);
            }
          }
          break;
        }
        default:
          break;
      }
    }
  }, [isNegativeNumber, type, getValues, name, setValue]);

  useEffect(() => {
    if (!onChange) return;
    watch((_value, { name }) => {
      onChange({ target: { name: name || '', value: getValues(name || '') } });
    });
  }, [getValues, name, onChange, watch]);

  return (
    <Controller
      name={name}
      control={control}
      defaultValue={defaultValue}
      render={({ field: { ref, ...fieldRest } }) => {
        return (
          <div
            className={classNames(
              'text-field',
              {
                'text-field--row': flexDirection === 'row'
              },
              {
                'text-field--error': errorMessage
              },
              {
                'text-field--readonly': readOnly
              },
              {
                'text-field--disabled': disabled
              },
              VARIANT_MAPS[variant],
              SIZE_MAPS[size],
              ALIGN_MAPS[align],
              COLOR_MAPS[color]
            )}
          >
            {label && <Label text={label} required={required} />}
            <div className="text-field__outside">
              <div className="text-field__actions">
                {iconName &&
                  (onClick ? (
                    <button
                      type="button"
                      className="text-field__icon-pressable focus-visible--outline"
                      onClick={onClick}
                    >
                      <Icon name={iconName} className="text-field__icon" />
                    </button>
                  ) : (
                    <Icon name={iconName} className="text-field__icon" />
                  ))}
                {type === 'currency' ||
                type === 'custom-date' ||
                type === 'date-to-today' ||
                type === 'phone-number' ? (
                  <NumberFormatBase
                    className="text-field__form"
                    getInputRef={ref}
                    placeholder={placeholder}
                    disabled={disabled}
                    autoComplete={autoComplete}
                    {...fieldRest}
                    {...(type === 'currency' && hasNegativeNumbers && { onKeyDown: onKeyDown })}
                    format={format(type)}
                    onValueChange={(values) => {
                      onInputChange(values.value);
                    }}
                    onChange={onChange}
                  />
                ) : (
                  <input
                    type={type}
                    className="text-field__form"
                    ref={ref}
                    placeholder={placeholder}
                    disabled={disabled}
                    autoComplete={autoComplete}
                    {...fieldRest}
                    value={fieldRest.value || ''}
                  />
                )}
                {!outside && unit && <Typography className="text-field__unit">{unit}</Typography>}
                {clearable && !disabled && !readOnly && fieldRest.value !== '' && (
                  <button
                    type="button"
                    className="text-field__clearable focus-visible--outline"
                    onClick={() => {
                      reset({ [name]: '' });
                      setIsNegativeNumber(false);
                    }}
                  >
                    <Icon
                      name="icon-check-hlep-fill-1"
                      size="text-24"
                      color="text-gray-700 hover:text-gray-600"
                    />
                  </button>
                )}
              </div>
              {!!outside && unit && (
                <Typography className="text-field__outside-unit">{unit}</Typography>
              )}
            </div>
            {errorMessage && <ErrorMessage text={errorMessage} />}
            {!errorMessage && showCurrencyValue && type === 'currency' && (
              <Typography variant="caption" className="text-field__currency-format">
                {formatToKoreanAmount(getValues(name) as string)}
              </Typography>
            )}
          </div>
        );
      }}
      {...props}
    />
  );
};

const Input = ({
  type = 'text',
  name,
  placeholder = '',
  disabled = false,
  readOnly = false,
  labelWidth,
  label,
  defaultValue,
  required = false,
  flexDirection = 'column',
  variant = 'bordered',
  size = 'default',
  align = 'left',
  color = 'primary',
  unit,
  outside = false,
  iconName,
  clearable = false,
  onClick,
  onChange,
  autoComplete = 'off',
  hasContext = true,
  hasNegativeNumbers = false,
  showCurrencyValue = false,
  ...props
}: InputProps) => {
  const [inputValue, setInputValue] = useState(defaultValue || '');

  const handleClear = useCallback(() => {
    setInputValue('');
    if (onChange) {
      onChange({ target: { name, value: '' } });
    }
  }, [name, onChange]);

  const handleChange = (
    e: ChangeEvent<HTMLInputElement> | { target: { name: string; value: string | number } }
  ) => {
    setInputValue(e.target.value);

    if (onChange) {
      onChange({ target: { name, value: e.target.value } });
    }
  };

  return (
    <>
      {hasContext ? (
        <ContextInput
          type={type}
          name={name}
          placeholder={placeholder}
          disabled={disabled}
          readOnly={readOnly}
          labelWidth={labelWidth}
          label={label}
          required={required}
          defaultValue={defaultValue}
          flexDirection={flexDirection}
          variant={variant}
          size={size}
          align={align}
          color={color}
          unit={unit}
          outside={outside}
          iconName={iconName}
          clearable={clearable}
          onClick={onClick}
          autoComplete={autoComplete}
          onChange={onChange}
          hasNegativeNumbers={hasNegativeNumbers}
          showCurrencyValue={showCurrencyValue}
        />
      ) : (
        <div
          className={classNames(
            'text-field',
            {
              'text-field--row': flexDirection === 'row'
            },
            {
              'text-field--readonly': readOnly
            },
            {
              'text-field--disabled': disabled
            },
            VARIANT_MAPS[variant],
            SIZE_MAPS[size],
            ALIGN_MAPS[align],
            COLOR_MAPS[color]
          )}
        >
          {label && <Label text={label} required={required} width={labelWidth} />}
          <div className="text-field__outside">
            <div className="text-field__actions">
              {iconName &&
                (onClick ? (
                  <button
                    type="button"
                    className="text-field__icon-pressable focus-visible--outline"
                    onClick={onClick}
                  >
                    <Icon name={iconName} className="text-field__icon" />
                  </button>
                ) : (
                  <Icon name={iconName} className="text-field__icon" />
                ))}
              <input
                name={name}
                className="text-field__form"
                type={type}
                placeholder={placeholder}
                value={inputValue || ''}
                onChange={handleChange}
                disabled={disabled}
                readOnly={readOnly}
                autoComplete={autoComplete}
                {...props}
              />
              {!outside && unit && <Typography className="text-field__unit">{unit}</Typography>}
              {clearable && !disabled && !readOnly && inputValue !== '' && (
                <button
                  type="button"
                  className="text-field__clearable focus-visible--outline"
                  onClick={handleClear}
                >
                  <Icon
                    name="icon-check-hlep-fill-1"
                    size="text-24"
                    color="text-gray-700 hover:text-gray-600"
                  />
                </button>
              )}
            </div>
            {!!outside && unit && (
              <Typography className="text-field__outside-unit">{unit}</Typography>
            )}
          </div>
        </div>
      )}
    </>
  );
};

const MemoizedInput = memo(Input);
export default MemoizedInput;
